setTimeout(()=> {
	amleneClient.sendToStream("A brand new Stream", {name : "Abdoulaye Sissokho", age : 35});
	amleneClient.sendToStream("A brand new Stream", {name : "Mamadou", age : 56});
	}, 5000);
